.magic-button {
  margin-top: 30px;
  background: linear-gradient(45deg, #e0e0e0 30%, #ffffff 90%); 
  border-radius: 50px;
  box-shadow: 0 3px 5px 2px rgba(200, 200, 200, 0.5);
  position: relative; 
  overflow: hidden;
  transition: transform 0.3s ease;
}

.magic-button:hover {
  transform: scale(1.05); 
}

.magic-button::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 300%;
  height: 300%;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 50%;
  transform: translate(-50%, -50%) scale(0); 
  transition: transform 0.5s ease;
  z-index: 0;
}

.magic-button:hover::before {
  transform: translate(-50%, -50%) scale(1);
}

.magic-button span {
  position: relative; 
  z-index: 1; 
  color: #333;
}

